.top-nav-bar .navbar-nav .nav-link {
  text-shadow: 0 1px 0 rgb(0 0 0 / 35%);
  margin-left: 10px;
  margin-right: 10px;
}

.top-nav-bar .navbar-nav .nav-link:hover {
  color: var(--theme-secondary) !important;
}

.top-nav-bar .nav-btn-a {
  margin-left: 10px;
  margin-right: 10px;
  /* font-weight: 700; */
  color: #000;
}

.top-nav-bar-dark .navbar-nav .nav-link {
  color: #fff !important;
}

.top-nav-bar-light .navbar-nav .nav-link {
  color: #000 !important;
}

.top-nav-bar-light {
  border-bottom: 1px solid rgba(0, 0, 0, 0.253) !important;
}

.top-nav-bar .language-btn img {
  display: inline;
  width: 30px;
}
